<template>
      <v-container>
          <v-row>
              <v-col cols="12">
                   <v-card>
        <v-card-title>
          <span class="headline">Profil Sayfam</span>
        </v-card-title>
        <v-card-text>
          <v-container>
             <v-alert
               v-if="err!=''"
      dense
      prominent
      text
      :type="'error'"
    >
    {{err}}
    </v-alert>
            <v-row>
              <v-col  cols="12" class="text-center">
  <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="newImage=null;imageData=''">
          <v-icon
          color="danger"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-close-box-multiple</v-icon>
        </span>
      </template>
      <span>Resimi Kaldır</span>
        </v-tooltip>
  <v-list-item-avatar size="170" color="grey darken-3">
          <v-img
            class="elevation-6"
            :src="imageData!=''?imageData:user.image==0?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':user.image"
            alt="Resim Bulunmamaktadır"
             ></v-img>
        </v-list-item-avatar>
    <input type="file" name="" @change="setImage" class="d-none" ref="fileInput" id="">
       <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="$refs['fileInput'].click()" class="ml-3">
          <v-icon
          color="primary"
          dark
          medium
          v-bind="attrs"
          v-on="on"
        >mdi-pencil-box-multiple</v-icon>
        </span>
      </template>
      <span>Düzenle</span>
        </v-tooltip>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field :error-messages="errName" :label="'Müdür Adı'" v-model="user.name" required></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field :error-messages="errLastname" :label="'Müdür Soyadı'"  v-model="user.lastName" hint="example of helper text only on focus"></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field
                :error-messages="errPhone"
                  :label="'Müdür Telefonu'"
                  hint="Telefon numarasını doğru formatta giriniz."
                   v-model="user.phone"

                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12">
              <h3>   Kullanıcı Adı : {{user.username}}  <small class="text-danger float-right">(Kullanıcı Adı değiştirilemez!)</small></h3>
              </v-col>
               <v-col cols="12" sm="12" md="12">
                <v-text-field
              v-model="password"
              filled
              :error-messages="errPassword"
              background-color="transparent"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
                :label="$t('generate.password')"
              :hint="$t('err.password')"
              counter
              @click:append="show1 = !show1"
            ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <small>*Girilmesi zorunlu alanlar</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn class="ma-2" :loading="loading" :disabled="loading" color="primary" @click="putUser()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
         <snack-bar :text="textMsg" :snackbar="snackbar" @finished="snackbar=$event"/>
      </v-card>
          </v-col>
          </v-row>
      <loading-dialog :dialogLoading="dialogLoading">Okul bilgilerini değiştirdiniz! Sizi giriş ekranına yönlendiriyoruz. Tekrardan giriş yaptığınızda bilgileriniz güncellenecektir.</loading-dialog>
      </v-container>
</template>

<script>
import { mapGetters } from 'vuex'
import loadingDialog from '../../components/dialogs/loadingDialogs'

export default {
    components:{
    loadingDialog
  },
data(){
  return{
    imageData:"",
    password:"",
    snackbar:false,
    newImage:null,
    successmessages:[],
    errormessages:[],
    loading:false,
    errPhone:[],
    err:"",
    errLastname:[],
    dialog3:false,
    newPass:"",
    errPassword:[],
    errName:[],
    textMsg:"",
    show1:false,
    confirm:false,
    dialogLoading:false
  }
},
computed:{
    ...mapGetters({
        'user':'getActiveUser'
    })
},
methods:{
  putUser(){
      this.err=''
    this.errPhone=[]
    this.errLastname=[]
    this.errName=[]
    this.loading=true
  if(this.newImage==null){
    if(this.user.name==''||this.user.lastName==''||this.user.phone==''||this.user.password.length<5){
      if(this.user.name==''){
     this.errName.push('Ad boş geçilemez.')
   }
    if(this.user.password==''){
     this.errPassword.push('Şifreniz en az 6 karakter olmalıdır.')
   }
   if(this.user.lastName==''){
     this.errLastname.push('Soyad boş geçilemez.')
   }
   if(this.user.phone.length<9){
          this.errPhone.push('Eksik veya yanlış bir telefon numarası girdiniz.')
    }
     this.loading=false
    }
    else{
        let user={
        name:this.user.name,
        lastName:this.user.lastName,
        phone:this.user.phone,
      }
      if(this.password.length>5){
          user.password = this.password
      }
      this.$api.put('user',this.user.id,user).then(res=>{
     if(res.result==1){
         this.textMsg="Müdür Başarıyla Güncellendi."
         this.snackbar=true
    this.loading=false
    this.dialogLoading=true
                       setTimeout(() => {
                         this.dialogLoading=false
                         localStorage.clear()
                         this.$router.push('/login')
                       }, 2000);
     }
     else{
        this.err = "Kullanıcı'da herhangi bir değişiklik yapmadınız."
    this.loading=false
     }

    })
    }
  }
  else{
    if(this.user.name==''||this.user.lastName==''||this.user.phone==''||this.user.password.length<5){
      if(this.user.name==''){
     this.errName.push('Ad boş geçilemez.')
   }
    if(this.user.password==''){
     this.errPassword.push('Şifreniz en az 6 karakter olmalıdır.')
   }
   if(this.user.lastName==''){
     this.errLastname.push('Soyad boş geçilemez.')
   }
   if(this.user.phone.length<9){
          this.errPhone.push('Eksik veya yanlış bir telefon numarası girdiniz.')
    }
     this.loading=false
    }
    else{
      let fd = new FormData()
    fd.append('image',this.newImage)
    fd.append('name',this.user.name)
    fd.append('lastName',this.user.lastName)
    fd.append('phone',this.user.phone)
    if(this.password.length>5){
         fd.append('password',this.password)
    }

    this.$api.putfd('user',this.user.id,fd).then(res=>{
      if(res.status=='success'){
        this.loading=false
        this.textMsg="Müdür Başarıyla Güncellendi."
         this.snackbar=true
         this.dialogLoading=true
                       setTimeout(() => {
                         this.dialogLoading=false
                         localStorage.clear()
                         this.$router.push('/login')
                       }, 2000);

      }
    })
    }

  }
  },
  setImage(ev){
    this.newImage = ev.target.files[0]
    let reader = new FileReader()
    var vm = this;

      reader.onload = e => {
        vm.imageData = e.target.result;
      };
      reader.readAsDataURL(ev.target.files[0]);
  }
}
}
</script>

